import React, {useEffect, useState} from 'react'
import Product from './Product'
import Entity from './Entity'
import { setCookie } from './cookie'
import { Link, Route, Routes } from 'react-router-dom'
import ProductTree from './pages/ProductTree'
import EntityTree from './pages/EntityTree'


export default function Header() {

    // var pathmap = new Map()
    // pathmap.set("/producttree","1")
    // pathmap.set("/entitytree","2")
    // pathmap.set("/product","3")
    // pathmap.set("/entity","4")
    // const tempgubun = pathmap.get(pathname)
    // if(tempgubun === null){
    //     tempgubun = "1"
    // }

    

    const pathname = window.location.pathname;
    const [currentProduct,setProduct] = useState()
    const [currentEntity,setEntity] = useState()
    const [gubun, setGubun] = useState("1");

    function handleLogout(){
        //localStorage.removeItem("token")
        setCookie("token", null);
        window.location.reload()
    }

    const goProductTree = () =>{        
        setGubun("1");
    }

    const goEntityTree = () =>{
        setGubun("2");
    }

    const goProduct = () =>{
        setGubun("3");
    }


    const goEntity = () =>{
        setGubun("4");
    }   
    
    
    return(
        <div style={{marginLeft:'10px', textAlign:'start'}}>
            <div>
                <h1>STEP Search Result
                <button style={{marginLeft: '5%'}} className='primary' onClick={handleLogout}>Logout</button>
                </h1>
                
            </div>        
            <div>
                <h2>
                    {gubun==="1"?
                        <Link to="/" className="button">Product Tree</Link>:
                        <Link to="/" onClick={goProductTree}  className="primary button">Product Tree</Link>
                    }
                    {gubun==="2"?
                        <Link to="/entitytree"  className="button">Entity Tree</Link>:
                        <Link to="/entitytree"  onClick={goEntityTree} className="primary button">Entity Tree</Link>
                    }
                    {gubun==="3"?
                        <Link to="/product"     className="button">Product Search</Link>:
                        <Link to="/product"     onClick={goProduct}      className="primary button">Product Search</Link>
                    }
                    {gubun==="4"?
                        <Link to="/entity"      className="button">Entity Search</Link>:
                        <Link to="/entity"      onClick={goEntity}       className="primary button">Entity Search</Link>
                    }
                </h2>
                
            </div>
            <Routes>
                <Route path="/"  element={<table><tbody><ProductTree key="Product hierarchy root" id="Product hierarchy root" name="Product hierarchy root" lvl="0"/></tbody></table>}/>
                <Route path="/entitytree"   element={<table><tbody><EntityTree key="Entity hierarchy root"   id="100305"  name="Entity root"lvl="0"/></tbody></table>}/>
                <Route path="/product"      element={<Product id={currentProduct} />}/>
                <Route path="/entity"       element={<Entity id={currentEntity} />}/>
            </Routes>
        </div>
    )
    
}
