import React, {useRef} from 'react'
import axios from 'axios';
import qs from 'qs'
import { setCookie } from './cookie';


export default function Login() {

    const idRef = useRef()
    const pwRef = useRef()

    

    function handleLoginClick(){
        const inputparms = {
            userId: idRef.current.value,
            password: pwRef.current.value
        }

        axios.post("https://apacpresales-hmckia-step.mdm.stibosystems.com/graphqlv2/auth",qs.stringify(inputparms),{
            headers: {
                "Content-Type": `application/x-www-form-urlencoded`,
                "Access-Control-Allow-Origin": 'apacpresales-hmckia-step.mdm.stibosystems.com'
                }
        }).then(res => {
            console.log(res.status)
            if(res.status === 200){
                //localStorage.setItem("token", response.data);
                const expires = new Date()
                expires.setSeconds(expires.getSeconds() + 6000)
                setCookie("token", res.data,{expires})
                window.location.reload();  
            }else{
                alert(res.data)
            }
            
        }).catch(err =>{
            alert(err)
        });
    }

    function handleEnter(){
        if(window.event.keyCode === 13){
            handleLoginClick();
        }
    }
    return (
    <div align="center" style={{marginTop:"10%"}}>
        <div className='alt'>
            <h1>STEP LOGIN</h1>
        </div>
        <div className='alt'>
            <input type="text" style={{width: "30%"}} ref={idRef} onKeyUp={handleEnter} placeholder="ID"></input>
            <input type="password" style={{width: "30%"}} ref={pwRef} onKeyUp={handleEnter} placeholder="PASSWORD"></input>
            <button className='primary' style={{width: "30%"}}  type='button' onClick={handleLoginClick}>Login</button>
        </div>
        
    </div>
    
    )
}
